:root {
  --bg-black: #0f0f0f;
  --white: #f8f8f8;
  --bg-grey: #272727;
  --bg-light-gray: rgba(39, 43, 45, 0.43);
  --bg-red: rgb(238, 47, 76);
  --bg-blue: rgb(26, 97, 233);
  --bg-purple: rgb(90, 30, 203);
  --mobile-box-shadow: rgba(250, 250, 255, 0.16) 0px -5px 6px 0px inset,
    rgba(38, 37, 37, 0.24) 4px 6px 11px 1px inset;
  --mobile-bg: rgb(14, 14, 15);
  --bg-green: rgb(2, 119, 87);
}
